@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-header{
    margin: 2rem;
    
    min-width:32rem;


}

.cabecera{
    background: linear-gradient(to left, lighten(v.$azul, 40%), v.$azul);
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    

    .titulo{
        display: flex;
        text-align: center;
        justify-content: space-around;
        
        h3{
            border-bottom: 1px, solid, v.$gris;
            margin-top: 2rem;
            font-size: 2rem;

            @include m.tablet {
                font-size: 2.8rem;
            }
            
        }      
        
    }

    h1, h2, h3{
        color:v.$grisOscuro;
        }

    h2{
        font-size: 2.2rem;
        @include m.tablet {
            font-size: 3.2rem;
        }
    }

    h1{
        font-size: 2.8rem;
        @include m.tablet {
            font-size: 4.2rem;
        }
    }
        
        
}
    
