/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  background-color: #FFFFFF;
  color: #1a1b15;
  min-height: 100%;
  text-align: center;
  justify-content: space-around;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Fira Sans Condensed", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 2rem auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Fira Sans Condensed", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

.contenedor-header {
  margin: 2rem;
  min-width: 32rem;
}

.cabecera {
  background: linear-gradient(to left, #cfedfd, #0da6f3);
  margin-bottom: 2rem;
  padding-bottom: 2rem;
}
.cabecera .titulo {
  display: flex;
  text-align: center;
  justify-content: space-around;
}
.cabecera .titulo h3 {
  border-bottom: 1px, solid, #929292;
  margin-top: 2rem;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .cabecera .titulo h3 {
    font-size: 2.8rem;
  }
}
.cabecera h1, .cabecera h2, .cabecera h3 {
  color: #333333;
}
.cabecera h2 {
  font-size: 2.2rem;
}
@media (min-width: 768px) {
  .cabecera h2 {
    font-size: 3.2rem;
  }
}
.cabecera h1 {
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  .cabecera h1 {
    font-size: 4.2rem;
  }
}

.contenedor-footer {
  margin: 2rem;
  min-width: 32rem;
  padding: 1rem;
  background: linear-gradient(to left, #cfedfd, #0da6f3);
}
.contenedor-footer .correo-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .contenedor-footer .correo-footer {
    padding: 0 2rem;
  }
}
.contenedor-footer h3, .contenedor-footer p, .contenedor-footer a {
  color: #333333;
}
.contenedor-footer h3 {
  margin-bottom: 1rem;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .contenedor-footer h3 {
    font-size: 2.8rem;
  }
}
.contenedor-footer p {
  font-weight: bold;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .contenedor-footer p {
    line-height: 1rem;
    font-size: 2.2rem;
  }
}
.contenedor-footer a:hover {
  color: #737373;
}