@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-footer{
    margin: 2rem;
    min-width: 32rem;
    padding: 1rem;
    background: linear-gradient(to left, lighten(v.$azul, 40%), v.$azul);

    .correo-footer{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include m.tablet {
            padding: 0 2rem;
        }
        
    }


    h3,p,a{
        color: v.$grisOscuro;
    }
    h3{
       margin-bottom: 1rem;
       font-size: 2rem;

       @include m.tablet {
           font-size: 2.8rem;
           
       }
    }
    p{
        font-weight: bold;
        font-size:1.5rem;
        
        @include m.tablet {
            line-height: 1rem;
            font-size: 2.2rem;
        }
    }

    a{
        
        &:hover{
            color:lighten(v.$grisOscuro, 25%);
        }
    }
}

